import React, {PropTypes} from 'react';
import { findDOMNode } from 'react-dom';
import KlassName from 'teapan/klassName';

function syncNodeAttributes(node, props) {
	if (props.selected) {
		node.setAttribute('tabindex', 0);
		node.setAttribute('selected', 'selected');
		if (props.focused) {
			node.focus();
		}
	} else {
		node.removeAttribute('tabindex');
		node.removeAttribute('selected');
	}
};

class TabBarItem extends React.Component {
	constructor(props) {
		super(props);
	}

	componentDidMount() {
		syncNodeAttributes(findDOMNode(this), this.props);
	}

	componentDidUpdate() {
		syncNodeAttributes(findDOMNode(this), this.props);
	}

	render() {
		const props = this.props;
		const {
			id,
			paneId,
			selected,
			disabled,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('tab-bar-item', {
			['selected']:('selected' in props && selected !== false),
			['disabled']:('disabled' in props && disabled !== false),
		},className);

		return (
			<li
				{...others}
				className={klassName}
				id={id}
				role="baritem"
				aria-selected={selected ? 'true' : 'false'}
				aria-expanded={selected ? 'true' : 'false'}
				aria-disabled={disabled ? 'true' : 'false'}
				aria-controls={paneId}
			>
				{children}
			</li>
		);
	}
};

TabBarItem.propTypes = {
	selected: PropTypes.bool,
	disabled: PropTypes.bool,

	id: PropTypes.string,
	paneId: PropTypes.string,
	className: PropTypes.string,
	children: PropTypes.oneOfType([
		PropTypes.array,
		PropTypes.object,
		PropTypes.string
	])
};

TabBarItem.defaultProps = {
	focused: false,
	selected: false,
	id: null,
	paneId: null
};

export default TabBarItem;